@use '../abstracts/index.scss' as *;

.logo {
  display: flex;
  align-items: center;
  gap: .25rem;
  font-family: Ubuntu;
  
  &__txt {
    font-weight: bold;
    font-style: italic;
    font-size: 1.6rem;
    letter-spacing: .1rem;
  }
  
  &__svg {
    $scale: 2rem;
    width: $scale;
    height: $scale;
    margin-bottom: .3rem;
    fill: lightgreen;
  }
}